<template>
    <div class="tag-input-wrapper">
        <el-tag
            v-for="tag in tagList"
            type="primary"
            :key="tag"
            :closable="true"
            :close-transition="true"
            @close="handleClose(tag)"
        >
            {{ tag }}
        </el-tag>
        <div
            class="tag-input"
            :class="{ placeholder: !tagList || !tagList.length }"
        >
            <textarea
                v-if="!tagList || !tagList.length"
                ref="textarea"
                v-model="inputVal"
                @blur.stop="blur"
                @keyup.prevent="validateInput"
                @keydown.enter.prevent="save"
                :placeholder="currentPlaceholder"
            ></textarea>
            <input
                v-else
                ref="input"
                class="new-tag"
                v-model="inputVal"
                @blur.stop="blur"
                @keyup.prevent="validateInput"
                @keydown.enter.prevent="save"
                :placeholder="currentPlaceholder"
            />
        </div>
    </div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep'

export default {
    name: 'inputTag',
    // 支持tags，需要自己绑定change处理值，支持v-model绑定
    props: {
        value: Array,
        tags: Array,
        placeholder: String,
        validate: String,
        iscanRepeat: Boolean // tags是否允许重复
    },
    data() {
        return {
            tagList: this.tags,
            currentPlaceholder:
                this.tags && this.tags.length ? '' : this.placeholder,
            inputVal: ''
        }
    },
    watch: {
        value(val) {
            this.tagList = cloneDeep(val)
        },
        tags(val) {
            this.tagList = cloneDeep(val)
        },
        tagList(val) {
            if (val && val.length) {
                this.currentPlaceholder = ''
            } else {
                this.currentPlaceholder = this.placeholder
            }
        }
    },

    methods: {
        // 失去焦点的时候如果input有值直接填充
        blur() {
            const value = this.inputVal
            if (value) {
                if (this.validate) {
                    // 有正则并且满足条件保存当前数据
                    const reg = new RegExp(this.validate)
                    if (reg.test(value)) {
                        this.save()
                    }
                } else {
                    // 无正则直接保存
                    this.save()
                }
            }
            this.$emit('blur')
        },

        handleClose(tag) {
            this.tagList.splice(this.tagList.indexOf(tag), 1)
            this.$emit('change', this.tagList)
            this.$emit('input', this.tagList)
        },

        autoFocus() {
            this.$nextTick(() => {
                this.inputVal = ''
                this.$refs.textarea && this.$refs.textarea.focus()
                this.$refs.input && this.$refs.input.focus()
            })
        },

        save() {
            if (!this.inputVal) {
                return
            }
            // 不可以重复
            if (
                !this.iscanRepeat &&
                this.tagList.indexOf(this.inputVal) !== -1
            ) {
                this.inputVal = ''
            } else {
                this.tagList.push(this.inputVal)
                this.inputVal = ''
                this.$emit('change', this.tagList)
                this.$emit('input', this.tagList)
            }
        },

        // 如果又传入正则，判断是否正确，没有通过检验，设置input的值为空
        validateInput(e) {
            const value = e.target.value
            if (this.validate && value) {
                const reg = new RegExp(this.validate)
                if (!reg.test(value)) {
                    e.target.value = ''
                    this.inputVal = ''
                }
            }
        }
    }
}
</script>

<style lang="less">
.tag-input-wrapper {
    border: 1px solid #e7e7eb;
    padding: 5px 7px;
    width: 100%;
    min-height: 50px;
    .el-tag {
        margin-right: 4px;
    }
    .tag-input {
        display: inline-block;
        &.placeholder {
            width: 100%;
            textarea {
                width: 100%;
                outline: none;
                border: none !important;
                resize: none;
                line-height: 1.4 !important;
                &::placeholder {
                    color: #d3d8e9;
                }
            }
        }
        .new-tag {
            background: transparent;
            border: 0 !important;
            color: #777;
            font-weight: 400;
            outline: none;
            width: 80px;
        }
    }
}
</style>
